<template>
  <div class="hello">
      <div class="parkinput">
            <van-cell-group >
            <van-field 
                v-model="value" 
                label="手机号"
                @blur="shows = false" 
                 @focus="show = true" 
                 placeholder="请输入用户名" 
                 ref="keyvalue" 
                 maxlength="11"
                 
            />

            <van-field
                v-if="stateShow"
                v-model="code"
                type="number"
                label="验证码"
                placeholder="请输入验证码"
                ref="code" 
            />
            </van-cell-group>

            
      </div>
      <div class="btnbox">
          <van-button size="large" class="surebtn" @click="surebtn">登录</van-button>
      </div>
      <van-number-keyboard
        :show="show"
        theme="custom"
        extra-key="#"
        close-button-text="完成"
        @blur="show = false"
        @input="onInput"
        @delete="onDelete"
        @close="onclose"
        />
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  name: 'HelloWorld',
  data () {
    return {
        stateShow:false,
        tell:"",
        code:"",
        value: '',
        showKeyboard: true,
        show: false
    }
  },
    methods: {
            onInput(value) {
                console.log(this);
                console.log(value);
                this.value = this.value+value;
                console.log(this.value);
                console.log(this.value.length);
                var keyNumlength=this.value.length;
                var tell=/^1[3|4|5|7|8][0-9]\d{8}$/;
                if(keyNumlength>11){
                    Toast('请输入正确的手机号!');
                }else if(keyNumlength==11&&tell.test(this.value)){
                    this.stateShow=true;
                    this.$emit('headCallBack', 'true'); 
                }else if(keyNumlength<11){
                    this.stateShow=false;
                }
            },
            onDelete() {
                this.value = this.value.slice(0, this.value.length - 1);
                console.log(this.$refs.keyvalue);
                var keyNumlength=this.value.length;
                if(keyNumlength<11){
                    this.stateShow=false;
                }
            },
            onclose(){
                console.log(this.value);
                this.$refs.keyvalue.blur();
            },
            surebtn(){
                var keyNumlength=this.value.length;
                var tell=/^1[3|4|5|7|8][0-9]\d{8}$/;
                if(keyNumlength!=11){
                    Toast("请输入正确的手机号！");
                }else if(!tell.test(this.value)){
                    Toast("请输入正确的手机号！");
                }else{
                    console.log("a");
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.parkinput{
    padding:20px 10px;
}
.surebtn{
    background: #16BFFF;
    color: #fff;
    border-radius: 10px;
    margin-top: 40px;
    width: 80%;
        height: 45px;
    line-height: 45px;
}
.btnbox{
    text-align: center;
}
</style>



