import Vue from 'vue'
import Router from 'vue-router'
import parkpayment from '@/components/parkpayment'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'parkpayment',
      component: parkpayment
    }
  ]
})



